@import url('https://fonts.googleapis.com/css2?family=Krona+One&family=Oswald&display=swap');

:root {
    font-size: 62.5%;
    box-sizing: border-box;
}
*,
*::before,
*::after {
    box-sizing: inherit;
    padding: 0;
    margin: 0;
}
body {
    font-size: 1.6rem;
    font-family: Oswald, sans-serif;
    background-color: #e9f5fa;
    min-height: 100vh;
    color: #4db0d3;
}
